.scores-mobile {
  padding-top: 100px;
}

.scores-mobile-item-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.scores-mobile-item-box:nth-of-type(1) {
  margin-top: 40px;
}

.scores-mobile-item {
  width: 710px;
}

.scores-mobile-item > p {
  font-size: 30px;
  line-height: 50px;
  color: #333;
}

.scores-mobile-item-divider {
  width: 710px;
}
